:host {
  width: 100%;
  height: 100%;
  display: flex;
}

.piano_keyboard {
  z-index: 0;
  background-color: #888;
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
}

.piano_keyboard-octave {
  --_pitch_tag_color: #004b3080;
  flex-grow: 1;
  position: relative;
}

.piano_keyboard-octave[octave="2"] {
  --_pitch_tag_color: #003d7280;
}

.piano_keyboard-octave[octave="3"] {
  --_pitch_tag_color: #006c6180;
}

.piano_keyboard-octave[octave="4"] {
  --_pitch_tag_color: #004b3080;
}

.piano_keyboard-octave[octave="5"] {
  --_pitch_tag_color: #6f1a5d80;
}

.piano_keyboard-octave[octave="6"] {
  --_pitch_tag_color: #79082e80;
}

.piano_keyboard .white_keybuttons {
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
}

.piano_keyboard .white_keybuttons .keybutton_container {
  flex-grow: 1;
  display: flex;
}

.piano_keyboard .black_keybuttons {
  width: 100%;
  height: 55%;
  position: absolute;
}

.piano_keyboard .keybutton_container .keybutton {
  width: 100%;
  height: 100%;
}

.piano_keyboard .black_keybuttons {
  pointer-events: none;
}

.piano_keyboard .black_keybuttons * {
  pointer-events: auto;
}

.piano_keyboard .black_keybuttons .keybutton_container {
  width: 8.57143%;
  height: 100%;
  position: absolute;
}

.piano_keyboard .black_keybuttons > * {
  transform: translateX(-50%);
}

.piano_keyboard .black_keybuttons > :first-child {
  left: 12.8571%;
}

.piano_keyboard .black_keybuttons > :nth-child(2) {
  left: 30%;
}

.piano_keyboard .black_keybuttons > :nth-child(3) {
  left: 55.7143%;
}

.piano_keyboard .black_keybuttons > :nth-child(4) {
  left: 71.4286%;
}

.piano_keyboard .black_keybuttons > :nth-child(5) {
  left: 87.1429%;
}

.piano_keyboard .keybutton {
  cursor: pointer;
  border-radius: 0 0 calc(3px + .1em) calc(3px + .1em);
  position: relative;
}

.piano_keyboard .white_keybutton {
  background-color: #ddd;
  border: 1px solid #888;
}

.piano_keyboard .black_keybutton {
  background-color: #222;
  border: 1px solid #888;
}

.piano_keyboard .keybutton:hover {
  transform: scale(1.05);
}

.piano_keyboard .keybutton.active {
  transform: scale(1);
}

.piano_keyboard .black_keybuttons {
  z-index: 2;
}

.piano_keyboard .white_keybutton:hover {
  z-index: 1;
}

.piano_keyboard .keybutton.active {
  background-color: #888;
}

.piano_keyboard .keybutton {
  transition: all .1s;
}

.piano_keyboard .keybutton .keybutton-pitch_tag {
  -webkit-user-select: none;
  user-select: none;
  color: #eee;
  pointer-events: none;
  background-color: #888;
  border-radius: calc(2px + .2em);
  padding: .25em;
  font-size: 1em;
  font-weight: bold;
  position: absolute;
  bottom: .6em;
  left: 50%;
  transform: translateX(-50%);
}

.piano_keyboard .white_keybutton .keybutton-pitch_tag {
  background-color: var(--_pitch_tag_color);
}

.piano_keyboard .black_keybutton .keybutton-pitch_tag, .piano_keyboard[hide_pitch_tag] .keybutton .keybutton-pitch_tag {
  display: none;
}



